.selectedPage {
    color: red;
    font-weight: bold;
}

.pageNumbers {
    font-size: 18px;
    margin: 0 5px 0 5px;
    cursor: default;

}

.paginator {
    margin: 10px 0 20px 0;
}

.pageNumber {
    padding: 5px;
    border: 1px solid #dadada;
    cursor: pointer;
    background-color: #ffffff;
}

.pageNumber.selectedPage {
    font-weight: bold;
    border-color: #1890ff;
    color: #1890ff;
}